.slider {
  cursor: pointer;
  background-color: #fff;
  border: .604701px solid #fd5103;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #fd5103;
  transition: all .4s;
  position: absolute;
  top: 2px;
  left: 1px;
}

input:checked + .slider {
  background-color: #fff;
}

input:focus + .slider {
  box-shadow: 0 0 1px #fff;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 20px;
}

.slider.round:before {
  border-radius: 50%;
}

